import Fuse from "fuse.js";
import { autorun, makeAutoObservable, reaction, runInAction, when } from "mobx";
import { CourseModel } from "../../models/CourseModel";
import { FilterModel } from "../../models/Filters/FilterModel";
import { getCourseFilters, getProgramFilters, getSpecialisationFilters } from "../../models/Filters/Filters";
import { ProgramModel } from "../../models/ProgramModel";
import { SpecialisationModel } from "../../models/SpecialisationModel";
import { SearchDataState } from "../../states/SearchDataState";

export interface ISearchQuery<T> {
  queryText: string,
  filters: FilterModel<T>[],
}

export interface ISearchResults {
  courseSearchResults: CourseModel[],
  programSearchResults: ProgramModel[],
  specialisationSearchResults: SpecialisationModel[],
}

export interface ISearchFilters {
  courseFilters: FilterModel<CourseModel>[],
  programFilters: FilterModel<ProgramModel>[],
  specialisationFilters: FilterModel<SpecialisationModel>[],
}

export enum SearchPanelItem {
  COURSES,
  PROGRAMS,
  SPECIALISATIONS,
}

type SearchableModel = ProgramModel | SpecialisationModel | CourseModel;

export class SearchPanelViewModel {
  constructor(
    public readonly searchDataState: SearchDataState,
  ) {
    makeAutoObservable<SearchPanelViewModel>(this)

    reaction(() => searchDataState.courseSearchData, () => {
      this.setIsLoading(false)
      this.searchFilters.courseFilters = getCourseFilters(searchDataState.courseSearchData)
    }, { fireImmediately: true })

    reaction(() => searchDataState.programSearchData, () => {
      this.setIsLoading(false)
      this.searchFilters.programFilters = getProgramFilters(searchDataState.programSearchData)
    }, { fireImmediately: true })

    reaction(() => searchDataState.specialisationSearchData, () => {
      this.setIsLoading(false)
      this.searchFilters.specialisationFilters = getSpecialisationFilters(searchDataState.specialisationSearchData)
    }, { fireImmediately: true })

    autorun(() => this.searchCourses(), { delay: 150 });

    autorun(() => this.searchPrograms(), { delay: 150 });

    autorun(() => this.searchSpecialisations(), { delay: 150 });
  }

  public queryText: string = ""
  public isLoading: boolean = true
  public resultsPerPage: number = 10
  public isInputActive: boolean = false
  public searchFilters: ISearchFilters = {
    courseFilters: [],
    programFilters: [],
    specialisationFilters: [],
  }
  public searchResults: ISearchResults = {
    courseSearchResults: [],
    programSearchResults: [],
    specialisationSearchResults: [],
  }
  public activeItem: SearchPanelItem = SearchPanelItem.COURSES

  public setActiveItem = (item: SearchPanelItem) => {
    this.activeItem = item
  }

  get shouldShowPanel() {
    return this.isInputActive || this.queryText.length > 0
  }

  setInputActive(isInputActive: boolean) {
    this.isInputActive = isInputActive;
  }

  setQueryText(queryText: string) {
    this.queryText = queryText
  }

  setIsLoading(isLoading: boolean) {
    this.isLoading = isLoading
  }

  setResultsPerPage(resultsPerPage: number) {
    this.resultsPerPage = resultsPerPage
  }

  public FUZZY_THRESHOLD: number = 0.4;

  private doFieldSearch<T extends SearchableModel>(data: T[], key: string, value: string): T[] {
    if (!key || !value) return [];

    // Fusejs include matching with 'value notation
    if (value[0] !== "'") value = `'${value}`;

    return new Fuse(data, { keys: [key], useExtendedSearch: true }).search(value).map((result => result.item));
  }

  private fieldSearch<T extends SearchableModel>(data: T[], searchableFields: { [key: string]: string }, searchText: string): [T[], string] {
    const newSearchText = searchText.split(" ").reduce((previous: string[], term: string) => {
      if (!term.includes(":")) return [...previous, term];

      const [key, value] = term.split(":");
      if (!key || !value) return [...previous, term];
      if (!searchableFields[key]) return [...previous, term];

      data = this.doFieldSearch(data, searchableFields[key], value);

      return previous;
    }, []).join(" ");
    return [data, newSearchText];
  }

  private search<T extends SearchableModel>(setOutput: (data: T[]) => {}, searchData: T[], sortFn: (a: T, b: T) => number, filters: FilterModel<T>[], searchableFields: { [key: string]: string }, defaultSearching: { name: string, weight: number }[]) {
    if (this.queryText === "") {
      setOutput(searchData.sort(sortFn));
      return;
    }

    let data = searchData;

    // Apply filters
    filters.forEach((filter) => {
      data = filter.applyFilter(data)
    })

    // Apply field matching
    let searchTextWithoutFields: string;
    [data, searchTextWithoutFields] = this.fieldSearch(data, searchableFields, this.queryText);

    if (searchTextWithoutFields) {
      const fuseFuzzySearcher = new Fuse(data, {
        keys: defaultSearching,
        shouldSort: true,
        threshold: this.FUZZY_THRESHOLD,
        useExtendedSearch: true,
      });

      data = fuseFuzzySearcher.search(searchTextWithoutFields).map((result => result.item));
    }

    setOutput(data);
  }

  searchCourses() {
    const setOutputFn = (data: CourseModel[]) => runInAction(() => this.searchResults.courseSearchResults = data)
    const sortFn = (a, b) => (a.code > b.code) ? 1 : -1
    this.search(
      setOutputFn,
      this.searchDataState.courseSearchData,
      sortFn,
      this.searchFilters.courseFilters,
      CourseModel.searchableFields,
      CourseModel.defaultSearching
    );
  }


  searchPrograms() {
    const setOutputFn = (data: ProgramModel[]) => runInAction(() => this.searchResults.programSearchResults = data)
    const sortFn = (a, b) => (a.title > b.title) ? 1 : -1
    this.search(
      setOutputFn,
      this.searchDataState.programSearchData,
      sortFn,
      this.searchFilters.programFilters,
      ProgramModel.searchableFields,
      ProgramModel.defaultSearching
    );
  }

  searchSpecialisations() {
    const setOutputFn = (data: SpecialisationModel[]) => runInAction(() => this.searchResults.specialisationSearchResults = data)
    const sortFn = (a, b) => (a.title > b.title) ? 1 : -1
    this.search(
      setOutputFn,
      this.searchDataState.specialisationSearchData,
      sortFn,
      this.searchFilters.specialisationFilters,
      SpecialisationModel.searchableFields,
      SpecialisationModel.defaultSearching
    );
  }
}
